<h2 id="gai-shu"><a class="header-anchor" href="#gai-shu" aria-hidden="true">¶</a> 概述</h2>
<p>布局的栅格化系统，我们是基于行（row）和列（col）来定义信息区块的外部框架，以保证页面的每个区域能够稳健地排布起来。下面简单介绍一下它的工作原理：</p>
<ul>
<li>通过<code v-pre="">row</code>在水平方向建立一组<code v-pre="">column</code>（简写col）</li>
<li>你的内容应当放置于<code v-pre="">col</code>内，并且，只有<code v-pre="">col</code>可以作为<code v-pre="">row</code>的直接元素</li>
<li>栅格系统中的列是指1到24的值来表示其跨越的范围。例如，三个等宽的列可以使用<code v-pre="">.col-8</code>来创建</li>
<li>如果一个<code v-pre="">row</code>中的<code v-pre="">col</code>总和超过 24，那么多余的<code v-pre="">col</code>会作为一个整体另起一行排列</li>
</ul>
<h2 id="flex-bu-ju"><a class="header-anchor" href="#flex-bu-ju" aria-hidden="true">¶</a> Flex 布局</h2>
<p>我们的栅格化系统支持 Flex 布局，允许子元素在父节点内的水平对齐方式 - 居左、居中、居右、等宽排列、分散排列。子元素与子元素之间，支持顶部对齐、垂直居中对齐、底部对齐的方式。同时，支持使用 order 来定义元素的排列顺序。</p>
<p>Flex 布局是基于 24 栅格来定义每一个『盒子』的宽度，但排版则不拘泥于栅格。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;div class=\&quot;grid-demo\&quot;&gt;\n        &lt;v-row&gt;\n            &lt;v-col span=\&quot;24\&quot;&gt;col-24&lt;/v-col&gt;\n        &lt;/v-row&gt;\n        &lt;v-row&gt;\n            &lt;v-col span=\&quot;6\&quot;&gt;col-6&lt;/v-col&gt;\n            &lt;v-col span=\&quot;6\&quot;&gt;col-6&lt;/v-col&gt;\n            &lt;v-col span=\&quot;6\&quot;&gt;col-6&lt;/v-col&gt;\n            &lt;v-col span=\&quot;6\&quot;&gt;col-6&lt;/v-col&gt;\n        &lt;/v-row&gt;\n        &lt;v-row&gt;\n            &lt;v-col span=\&quot;8\&quot;&gt;col-8&lt;/v-col&gt;\n            &lt;v-col span=\&quot;8\&quot;&gt;col-8&lt;/v-col&gt;\n            &lt;v-col span=\&quot;8\&quot;&gt;col-8&lt;/v-col&gt;\n        &lt;/v-row&gt;\n        &lt;v-row&gt;\n            &lt;v-col span=\&quot;12\&quot;&gt;col-12&lt;/v-col&gt;\n            &lt;v-col span=\&quot;12\&quot;&gt;col-12&lt;/v-col&gt;\n        &lt;/v-row&gt;\n\n        &lt;v-row class=\&quot;demo-row\&quot;&gt;\n            &lt;v-col span=\&quot;16\&quot;&gt;col-16&lt;/v-col&gt;\n            &lt;v-col span=\&quot;8\&quot;&gt;col-8&lt;/v-col&gt;\n        &lt;/v-row&gt;\n    &lt;/div&gt;\n&lt;/template&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
