                  <div slot="description"><h4 id="ji-ben-zha-ge"><a class="header-anchor" href="#ji-ben-zha-ge" aria-hidden="true">¶</a> 基本栅格</h4>
<p>从堆叠到水平排列。<br>
使用单一的一组 <code>Row</code> 和 <code>Col</code> 栅格组件，就可以创建一个基本的栅格系统，所有列（Col）必须放在 <code>Row</code> 内。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"grid-demo"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-row</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">v-col</span> <span class="hljs-attr">span</span>=<span class="hljs-string">"24"</span>&gt;</span>col-24<span class="hljs-tag">&lt;/<span class="hljs-name">v-col</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">v-row</span>&gt;</span>
