t;\n            &lt;v-col span=\&quot;4\&quot;&gt;col-4&lt;/v-col&gt;\n            &lt;v-col span=\&quot;4\&quot;&gt;col-4&lt;/v-col&gt;\n            &lt;v-col span=\&quot;4\&quot;&gt;col-4&lt;/v-col&gt;\n        &lt;/v-row&gt;\n    &lt;/div&gt;\n&lt;/template&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
