                  <div slot="description"><h4 id="flex-bu-ju"><a class="header-anchor" href="#flex-bu-ju" aria-hidden="true">¶</a> Flex 布局</h4>
<p>Flex 布局基础。<br>
使用 <code>row-flex</code> 定义 <code>flex</code> 布局，其子元素根据不同的值 <code>start</code>,<code>center</code>,<code>end</code>,<code>space-between</code>,<code>space-around</code>，分别定义其在父节点里面的排版方式。</p>
</div>
