        Column Count: <span class="hljs-tag">&lt;<span class="hljs-name">v-select</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"lg"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: 200px;"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"colCountOptions"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"colCount"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"colCountChange"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-select</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-row</span> <span class="hljs-attr">:gutter</span>=<span class="hljs-string">"gutter"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">v-col</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"n in colCount"</span> <span class="hljs-attr">:span</span>=<span class="hljs-string">"24/colCount"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"n"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"gutter-row"</span> &gt;</span>
                <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"gutter-box"</span>&gt;</span>Column<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
            <span class="hljs-tag">&lt;/<span class="hljs-name">v-col</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">v-row</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">pre</span>&gt;</span><span>{{</span> renderCode <span>}}</span><span class="hljs-tag">&lt;/<span class="hljs-name">pre</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">gutter</span>: <span class="hljs-number">0</span>,
                <span class="hljs-attr">colCount</span>: <span class="hljs-number">2</span>,
                <span class="hljs-attr">renderCode</span>: <span class="hljs-string">'&lt;v-row&gt;\n'</span> +
                            <span class="hljs-string">'    &lt;v-col span="12"&gt;&lt;/v-col&gt;\n'</span> +
                            <span class="hljs-string">'    &lt;v-col span="12"&gt;&lt;/v-col&gt;\n'</span> +
                            <span class="hljs-string">'&lt;/v-row&gt;'</span>,
                <span class="hljs-attr">gutterOptions</span>: [
                    {<span class="hljs-attr">label</span>: <span class="hljs-string">'0px'</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">0</span>},
                    {<span class="hljs-attr">label</span>: <span class="hljs-string">'8px'</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">8</span>},
                    {<span class="hljs-attr">label</span>: <span class="hljs-string">'16px'</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">16</span>},
                    {<span class="hljs-attr">label</span>: <span class="hljs-string">'24px'</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">24</span>},
                    {<span class="hljs-attr">label</span>: <span class="hljs-string">'32px'</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">32</span>},
                    {<span class="hljs-attr">label</span>: <span class="hljs-string">'40px'</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">40</span>},
                    {<span class="hljs-attr">label</span>: <span class="hljs-string">'48px'</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">48</span>}
                ],
                <span class="hljs-attr">colCountOptions</span>: [
                    {<span class="hljs-attr">label</span>: <span class="hljs-string">'2'</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">2</span>},
                    {<span class="hljs-attr">label</span>: <span class="hljs-string">'3'</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">3</span>},
                    {<span class="hljs-attr">label</span>: <span class="hljs-string">'4'</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">4</span>},
                    {<span class="hljs-attr">label</span>: <span class="hljs-string">'6'</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">6</span>},
                    {<span class="hljs-attr">label</span>: <span class="hljs-string">'8'</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">8</span>},
                    {<span class="hljs-attr">label</span>: <span class="hljs-string">'12'</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">12</span>}
                ],
            }
        },
        <span class="hljs-attr">methods</span>: {
            gutterChange(value) {
                <span class="hljs-keyword">this</span>.refreshCode(value, <span class="hljs-keyword">this</span>.colCount);
            },
            colCountChange(value) {
                <span class="hljs-keyword">this</span>.refreshCode(<span class="hljs-keyword">this</span>.gutter, value);
            },
            refreshCode(gutter, colCount) {
                <span class="hljs-keyword">this</span>.renderCode = <span class="hljs-string">''</span>;
                <span class="hljs-keyword">let</span> gutterStr = <span class="hljs-string">""</span>;
                <span class="hljs-keyword">if</span> (gutter) gutterStr = <span class="hljs-string">' gutter="'</span> + gutter + <span class="hljs-string">'"'</span>;
                <span class="hljs-keyword">this</span>.renderCode += <span class="hljs-string">'&lt;v-row'</span> + gutterStr + <span class="hljs-string">'&gt;\n'</span>;
                <span class="hljs-keyword">for</span> (<span class="hljs-keyword">let</span> n = <span class="hljs-number">0</span>; n &lt; colCount; n++) {
                    <span class="hljs-keyword">this</span>.renderCode += <span class="hljs-string">'    &lt;v-col span="'</span> + (<span class="hljs-number">24</span> / colCount) + <span class="hljs-string">'"&gt;&lt;/v-col&gt;\n'</span>;
                }
                <span class="hljs-keyword">this</span>.renderCode += <span class="hljs-string">'&lt;/v-row&gt;'</span>;
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</demo-box>
<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="row-props"><a class="header-anchor" href="#row-props" aria-hidden="true">¶</a> Row Props</h3>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>gutter</td>
<td>栅格间隔</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>type</td>
<td>布局模式，可选<code v-pre="">flex</code>，现代浏览器下有效</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>align</td>
<td>flex 布局下的垂直对齐方式：top middle bottom</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>justify</td>
<td>flex 布局下的水平排列方式：start end center space-around space-between</td>
<td>String</td>
<td>start</td>
</tr>
</tbody>
</table>
<h3 id="col-props"><a class="header-anchor" href="#col-props" aria-hidden="true">¶</a> Col Props</h3>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>span</td>
<td>栅格占位格数，为 0 时相当于 display: none</td>
<td>Number</td>
<td>-</td>
</tr>
<tr>
<td>order</td>
<td>栅格顺序，flex 布局模式下有效</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>offset</td>
<td>栅格左侧的间隔格数，间隔内不可以有栅格</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>push</td>
<td>栅格向右移动格数</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>pull</td>
<td>栅格向左移动格数</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>xs</td>
<td>&lt;768px 响应式栅格，可为栅格数或一个包含其他属性的对象</td>
<td>Number|Object</td>
<td>-</td>
</tr>
<tr>
<td>sm</td>
<td>≥768px 响应式栅格，可为栅格数或一个包含其他属性的对象</td>
<td>Number|Object</td>
<td>-</td>
</tr>
<tr>
<td>md</td>
<td>≥992px 响应式栅格，可为栅格数或一个包含其他属性的对象</td>
<td>Number|Object</td>
<td>-</td>
</tr>
<tr>
<td>lg</td>
<td>≥1200px 响应式栅格，可为栅格数或一个包含其他属性的对象</td>
<td>Number|Object</td>
<td>-</td>
</tr>
</tbody>
</table>
</undefined></template>
<style scoped>
    .grid-demo .demo-row {
        background-image: linear-gradient(90deg, #F5F5F5 4.16666667%, transparent 4.16666667%, transparent 8.33333333%, #F5F5F5 8.33333333%, #F5F5F5 12.5%, transparent 12.5%, transparent 16.66666667%, #F5F5F5 16.66666667%, #F5F5F5 20.83333333%, transparent 20.83333333%, transparent 25%, #F5F5F5 25%, #F5F5F5 29.16666667%, transparent 29.16666667%, transparent 33.33333333%, #F5F5F5 33.33333333%, #F5F5F5 37.5%, transparent 37.5%, transparent 41.66666667%, #F5F5F5 41.66666667%, #F5F5F5 45.83333333%, transparent 45.83333333%, transparent 50%, #F5F5F5 50%, #F5F5F5 54.16666667%, transparent 54.16666667%, transparent 58.33333333%, #F5F5F5 58.33333333%, #F5F5F5 62.5%, transparent 62.5%, transparent 66.66666667%, #F5F5F5 66.66666667%, #F5F5F5 70.83333333%, transparent 70.83333333%, transparent 75%, #F5F5F5 75%, #F5F5F5 79.16666667%, transparent 79.16666667%, transparent 83.33333333%, #F5F5F5 83.33333333%, #F5F5F5 87.5%, transparent 87.5%, transparent 91.66666667%, #F5F5F5 91.66666667%, #F5F5F5 95.83333333%, transparent 95.83333333%);
        overflow: hidden;
        margin-bottom: 8px;
    }

    .grid-demo .ant-row-flex {
        background: #F5F5F5;
    }

    .grid-demo .ant-row > div,
    .grid-demo .ant-row-flex > div {
        padding: 5px 0;
        text-align: center;
        border-radius: 0;
        min-height: 30px;
        margin-top: 8px;
        margin-bottom: 8px;
        color: #fff;
    }

    .grid-demo .ant-row > div:not(.gutter-row),
    .grid-demo .ant-row-flex > div:not(.gutter-row) {
        background: #00A0E9;
        padding: 16px 0;
    }

    .grid-demo .ant-row > div:not(.gutter-row):nth-child(2n+1),
    .grid-demo .ant-row-flex > div:not(.gutter-row):nth-child(2n+1) {
        background: rgba(0, 160, 233, 0.7);
    }

    .grid-demo .ant-row .demo-col {
        text-align: center;
        padding: 30px 0;
        color: #fff;
        font-size: 18px;
        border: none;
        margin-top: 0;
        margin-bottom: 0;
    }

    .grid-demo .overview .ant-row .demo-col-1 {
        background: rgba(0, 160, 233, 0.7);
    }

    .grid-demo .overview .ant-row .demo-col-2 {
        background: rgba(0, 160, 233, 0.5);
    }

    .grid-demo .overview .ant-row .demo-col-3 {
        background: rgba(255, 255, 255, 0.2);
        color: #999;
    }

    .grid-demo .overview .ant-row .demo-col-4 {
        background: rgba(0, 160, 233, 0.6);
    }

    .grid-demo .overview .ant-row .demo-col-5 {
        background: rgba(255, 255, 255, 0.5);
        color: #999;
    }

    .grid-demo .height-100 {
        height: 100px;
        line-height: 100px;
    }

    .grid-demo .height-50 {
        height: 50px;
        line-height: 50px;
    }

    .grid-demo .height-120 {
        height: 120px;
        line-height: 120px;
    }

    .grid-demo .height-80 {
        height: 80px;
        line-height: 80px;
    }

    .grid-demo .gutter-example .ant-row > div {
        background: transparent;
        border: 0;
    }

    .grid-demo .gutter-box {
        background: #00A0E9;
        padding: 5px 0;
    }
</style>
<script>
    export default {
        data: function() {
            return {
                align: "top",
                index: 0,
                alignChoose: ["top", "middle", "bottom"],
                gutter: 0,
                colCount: 2,
                renderCode: '<v-row>\n' +
                            '    <v-col span="12"></v-col>\n' +
                            '    <v-col span="12"></v-col>\n' +
                            '</v-row>',
                gutterOptions: [
                    {label: '0px', value: 0},
                    {label: '8px', value: 8},
                    {label: '16px', value: 16},
                    {label: '24px', value: 24},
                    {label: '32px', value: 32},
                    {label: '40px', value: 40},
                    {label: '48px', value: 48}
                ],
                colCountOptions: [
                    {label: '2', value: 2},
                    {label: '3', value: 3},
                    {label: '4', value: 4},
                    {label: '6', value: 6},
                    {label: '8', value: 8},
                    {label: '12', value: 12}
                ],
            }
        },
        methods: {
            alignChange() {
                this.index = (this.index + 1) % 3;
                this.align = this.alignChoose[this.index];
            },
            gutterChange(value) {
                this.refreshCode(value, this.colCount);
            },
            colCountChange(value) {
                this.refreshCode(this.gutter, value);
            },
            refreshCode(gutter, colCount) {
                this.renderCode = '';
                let gutterStr = "";
                if (gutter) gutterStr = ' gutter="' + gutter + '"';
                this.renderCode += '<v-row' + gutterStr + '>\n';
                for (let n = 0; n < colCount; n++) {
                    this.renderCode += '    <v-col span="' + (24 / colCount) + '"></v-col>\n';
                }
                this.renderCode += '</v-row>';
            }
        }
    }
</script>


